<template>
    <div id="content_wrap">
        <Header2 breadcrumbs="  ">
        </Header2>
        <div class="notiMessage">
                  !
        </div>

        <div class="formBox">
            <div class="photoColumn col-md-6">
                <div class="subTitle">
                      
                </div>
                <div class="photo file-upload">
                      <div v-if="!image">
                          <label for="ex_file">
                              <img src="../assets/ic-add.svg" class="ic-add">
                          </label>
                        <input type="file" @change="onFileChange" class="upload">
                      </div>
                      <div v-else>
                        <img :src="image" class="coverImg"/>
                        <button @click="removeImage" class="removeBtn">
                            <img src="../assets/ic-clear.svg" class="icClear">
                        </button>
                      </div>
                </div>
            </div>
            <div class="select-group col-md-6">
                <div class="form-group">
                    <label>
                        
                    </label>
                    <select v-model="lang_type">
                        <option value="ko_KR">
                            
                        </option>
                        <option value="en_US">
                            English
                        </option>
                        <option value="zh_CN">
                            ()
                        </option>
                        <option value="ja_JP">
                            
                        </option>
                    </select>
                </div>
                <div class="form-group">
                    <label>
                         
                    </label>
                    <select v-model="choose_star">
                        <option v-for="star in starList" v-bind:value="star.STAR_IDX">
                            <!--  choose star .  vue.js    html   .  .
                            v-bind value    value .      choose_star -->
                            {{ star.STAR_NAME }}
                            <!--   -->
                        </option>
                    </select>
                </div>
                <div class="form-group">
                    <label>
                         
                    </label>
                    <span class="charactersInfo"></span>
                    <div class="formControlBox">
                        <input v-model="title" class="formControl yourtextarea" maxlength="30" name="workTitle" type="text" required placeholder="  ">
                        <div class="validateAlert" v-if="title.length == 0">
                            <span>  </span>
                        </div>
                    </div>
                </div>
                <div class="form-group">
                    <label>
                         
                    </label>
                    <span class="charactersInfo2"></span>
                    <div class="formControlBox">
                        <textarea v-model="description" class="formControl introText yourtextarea2"maxlength="300" name="workTitle" type="text" required placeholder="   ">
                        </textarea>
                        <div class="validateAlert" v-if="description.length == 0">
                            <span>  </span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="btnCenter">
            <button class="btn_publish" id="submit" v-on:click="submit">
                 
            </button>
        </div>
    </div>
</template>

<script type="text/javascript">

import Header2 from '@/components/Header';
import axios from 'axios';

export default {
    name: 'WorkUpload',
    components: { Header2 },
    beforeCreate() {
    },
    created() { // 
        this.getStarList();
    },
    beforeMount() {
    },

    mounted() {
        $(".yourtextarea").keyup(function(){
            $(".charactersInfo").text(($(this).val().length)+"/30");
        });
        $(".yourtextarea2").keyup(function(){
            $(".charactersInfo2").text(($(this).val().length)+"/300");
        });
    },
    data() {
        return{
            identity:'9f1129ef534560b29b9e0ab1b5e27fa1595fba9b00db3e094391bea6255cc6cc',
            title:'',
            description: '',
            choose_star: null, //        .
            lang_type: "ko_KR",
            image: null,
            imageFile: null,
            starList: [], //     .
            model: {//   .      .
                upload_phone: null,
                identity: null,
                title: null,
                description: null,
                lang_type: null,
                choose_stars: []
            }
        };
    },
    methods: {
        onFileChange(e) {
            var files = e.target.files || e.dataTransfer.files; // || -> or.       .     .
            if (!files.length) {
                return;
            }
            this.createImage(files[0]);//   . files[0]=  
            this.imageFile = files[0]//       .
        },
        createImage(file) { //    
            var image = new Image();
            var reader = new FileReader();
            var vm = this;

            reader.onload = (e) => {
                vm.image = e.target.result;
            };
            reader.readAsDataURL(file);
        },
        removeImage: function (e) {
            this.image = null;
        },
        submit: function(){
            // Object.keys()   
            // Objecy.keys()  argument:Object key  .
            const formData = new FormData()
            formData.append('upload_photo', this.imageFile);
            formData.append('identity', this.identity);
            formData.append('title', this.title);
            formData.append('description', this.description);
            formData.append('lang_type', this.lang_type);
            formData.append('choose_star[]', this.choose_star)

            //

            axios({
                method: 'POST',
                url: '/api/v8/AddFanfic',
                data: formData,//=body
                headers: {
                    'Content-Type': 'multipart/form-data'//body       .
                },
            },
            ).then((response) => { //response    . = function(response) {}
                    console.log(response);
                    alert(' .');
                    var fanfic_idx = response.data.message;
                    this.$router.push(`/work/detail/${fanfic_idx}`)
                }
            )
        },
        getStarList() {
            const baseURI2 = 'api/v2/GetSubscriptionList'; //           . (  )
            var lang = "ko";
            var userId = '9f1129ef534560b29b9e0ab1b5e27fa1595fba9b00db3e094391bea6255cc6cc';
            var packageName = "com.photocard.master";
            this.$http.get(`${baseURI2}/${lang}/${userId}/${packageName}`)// . (=url).     .
            .then((result) => { //,  ~.  (result) . function(result){} .
                console.log(result)
                this.starList = result.data // vue.this~  result  .  vue   . result.data.
                this.choose_star = result.data[0].STAR_IDX
                //0       value     .   . STAR_IDX   , v-bind:value   .
            })
        }
    },
};



</script>


<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.validateAlert{
    text-align: right;
    margin-top: 4px;
    font-size: 14px;
    color: #ff5656;
}
.icClear{
    width: 16px;
    height: 16px;
}
.removeBtn{
    width: 24px;
	height: 24px;
	background-color: #ffffff;
    border-radius: 50%;
    border: solid 1px #ced4da;
    position: absolute;
    right: 38px;
    top: 44px;
    text-align: center;
    padding: 0px;
}
input.upload {
  opacity: 0;
  position: relative;
  width: 48px;
  height: 48px;
  margin: 0;
  position: absolute;
  top: 55%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
}
#content_wrap{
    width: 1000px;
    margin: auto;
}
.introText{
    height: 221px;
    -webkit-appearance: none;
}
.charactersInfo{
    float: right;
    color: #ced4da;
}
.charactersInfo2{
    float: right;
    color: #ced4da;
}

.ic-add{
    width: 48px;
	height: 48px;
    margin: 0;
    position: absolute;
    top: 55%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
}
.notiMessage{
    font-size: 24px;
    text-align: center;
    color: #343a40;
    margin-top: 24px;
    margin-bottom: 40px;
}
.form-group{
    margin: 0px;
}
label{
    font-size: 16px;
    text-align: left;
	color: rgba(0, 0, 0, 0.87);
    font-weight: normal;
    margin-bottom: 8px;
}
.form-group select{
    background-image: url("../assets/ic_expand_more.svg");
    background-repeat: no-repeat;
    background-position: right 16px center;
    background-size: 12px;
    -webkit-appearance: none;
    border-radius: 5;
    line-height: 2;
    padding: 12px 16px;
    border: 1px solid #ced4da;
    width: 100%;
    background-color: #fff;
    font-size: 16px;
    margin-bottom: 24px;
    color: #343a40;
}
.formBox{
    width: 740px;
    margin: auto;
}
.photo{
    width: 328px;
	height: 224px;
	border-radius: 5px;
	border: solid 1px #ced4da;
}
.coverImg{
  width: 328px;
  height: 224px;
  margin: auto;
}
.subTitle{
    font-size: 16px;
    margin-bottom: 8px;
    color: #343a40;
}
.formControlBox{
    margin-bottom: 24px;
}
.formControl{
    border-radius: 5px;
    padding: 16px;
    border: 1px solid #ced4da;
    width: 100%;
    background-color: #fff;
    font-size: 16px;
    color: #343a40;
}
::-webkit-input-placeholder {
  color: #ced4da;
}
.btn_publish{
    padding: 16px 60px;
    font-size: 16px;
	text-align: center;
	color: #ffffff;
    border-radius: 33.5px;
	background-color: #ff5656;
	box-shadow: 0 3px 6px 0 rgba(255, 86, 86, 0.63);
    font-weight: lighter;
    border-style: none;
    margin-top: 16px;
}
.btnCenter{
    text-align: center;
}
</style>



